import React from "react";
import styled from "styled-components";
import {LoadingOutlined} from "@ant-design/icons";

export default ({
  className,
  htmlType,
  children,
  icon,
  loading,
  ...props
}) => (
  <StyledButton
    {...props}
    loading={loading}
    type={htmlType}
    className={className}
  >
    <div className="flex-h flex-center">
      {!!loading && (<div className="flex-v mr10"><LoadingOutlined /></div>)}
      <div>{children}</div>
    </div>
  </StyledButton>
);

const StyledButton = styled.button`
  display: inline-block;
  border: 0;
  font-style: normal;
  font-weight: 600;
  outline: none;
  cursor: pointer;
  white-space: nowrap;
  border: 1px solid;
  border-radius: 5px;
  &:hover {
    color: white;
  }

  &:active {
    opacity: 0.8;
  }

  ${props => props.width && `width: ${(props) => {
    if (String(props.width).indexOf("%") !== -1) {
      return props.width;
    }
    if (String(props.width).indexOf("px") !== -1) {
      return `${props.width}px !important;`;
    }
    return props.width;
  }}`}
  ${props => !!props.disabled && `
    opacity: 0.5;
  `}

  ${props => ({
    m: `
      font-size: 16px;
      line-height: 24px;
      padding: 10px 16px;
    `,
    s: `
      font-size: 12px;
      line-height: 14px;
      padding: 4px 9px;
    `,
    xs: `
      font-size: 10px;
      line-height: 12px;
      padding: 2px 9px;
    `,

    default: `
      padding: 7px 15px;
      font-size: 14px;
      line-height: 24px !important;
    `,
  })[props.size || "default"]}

  ${props => `
    color: white;
    ${({
    primary: `
      background: #30318C;
      border-color: #30318C;
    `,
    success: `
      background: #009F4F;
      border-color: #009F4F;
    `,
    error: `
      background: #FF4D4F;
      border-color: #FF4D4F;
    `,
    warning: `
      background: #FFC700;
      border-color: #FFC700;
    `,
    gray: `
      background: #F5F5F5;
      border-color: white;
      color: #373737 !important;
    `,
    default: `
      background: white;
      color: #30318C !important;
    `,
  })[props.background || "default"]}`}

  ${props => ({
    primary: `
      border-color: #30318C;
      color: #30318C !important;
      background: transparent;
    `,
    success: `
      border-width: 1px;
      border-color: #009F4F;
      color: #009F4F !important;
      background: transparent;
    `,
    error: `
      border-color: #FF4D4F;
      color: #FF4D4F !important;
      background: transparent;
    `,
    warning: `
      border-color: #FFC700;
      color: #FFC700 !important;
      background: transparent;
    `,
    black: `
      border-color: #373737;
      color: #373737 !important;
      background: transparent;
    `,
    white: `
      background: transparent;
      border-color: white;
      color: white !important;
    `,
    default: `
      border: 0px solid #5426ff !important;
      color: white !important;
      background-color: rgb(84, 105, 212);
      box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px,
        rgba(0, 0, 0, 0) 0px 0px 0px 0px,
        rgba(0, 0, 0, 0.12) 0px 1px 1px 0px,
        rgb(84, 105, 212) 0px 0px 0px 1px,
        rgba(0, 0, 0, 0) 0px 0px 0px 0px,
        rgba(0, 0, 0, 0) 0px 0px 0px 0px,
        rgba(60, 66, 87, 0.08) 0px 2px 5px 0px;
      &:hover {
        transition: all 0.1s;
      }
    `,
  })[props.border || "default"]}
`;
